.Board-wrapper {
    padding-top: 114px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.Board {
    width: 270px;
}

.row-center {
    border-top: 2px solid #888;
    border-bottom: 2px solid #888;
}

@media (min-width: 520px) {

    .Board-wrapper {
        padding-top: 152px;
        padding-bottom: 16px;
    }

    .Board {
        width: 480px;
    }
    .row-center {
        border-top: 4px solid #888;
        border-bottom: 4px solid #888;
    }
}



