.Player {
    width: 52px;
    height: 52px;
}

.animated {
    -webkit-animation: spinning-x infinite 1.5s linear;
            animation: spinning-x infinite 1.5s linear;
}

@-webkit-keyframes spinning-x {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spinning-x {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (min-width: 520px) {

    .Player {
        width: 90px;
        height: 90px;
    }
}



